body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  background-color: #f6f9fc;
  color: #667;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

a {
  text-decoration: none;
  outline: none;
}

h1,
h2,
h3 {
  text-transform: uppercase;
  margin-top: 0;
}

.header {
  z-index: 1;
  height: 60px;
  line-height: 60px;
  padding: 0 20px;
  width: 100%;
  position: fixed;
  top: 0;
  background-color: #272a30;
  font-weight: 700;
  font-size: 18px;
  text-align: center;
  border-bottom: 1px solid #333;
}

.header > * {
  display: inline-block;
  color: #fff;
  text-decoration: none;
  margin: 0 10px;
}

.container {
  padding: 0 4px;
  position: relative;
  max-width: 580px;
  width: 100%;
  margin: -100px auto 0 auto;
}

#footer {
  width: 100%;
  line-height: 60px;
  font-size: 12px;
  text-align: center;
  color: #888;
}

#footer a {
  color: #000;
}

.launches-title {
  margin: 16px;
  color: #fff;
  font-weight: 400;
  padding-top: 8px;
  text-transform: unset;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.2);
}



